<template>
    <div class="baca-pesan-container">
        <p class="baca-pesan-label">Kami menemukan pesan untukmu</p>
        <div class="baca-pesan-konten my-2">
            {{ getPesan }}
        </div>
        <button class="btn-blue mx-0" style="width:100%" @click="handleTemukanPesan(getSelectedKategori,getSelectedTema)">Temukan Pesan Lainnya</button>
        <div class="baca-pesan-action my-2">
            <router-link to="/temukan-pesan" class="float-right"><small>Cari Kategori Lainnya</small></router-link>
            <router-link to="/"><small>&#8592; Kembali ke Halaman Utama</small></router-link>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    computed: {
        ...mapGetters([
            'getPesan','getSelectedKategori','getSelectedTema'
        ])
    },
    methods: {
        ...mapActions([
            'temukanPesan'
        ]),
        handleTemukanPesan(kategori,tema){
            if(kategori === ''){
                alert('Kategori masih kosong, harap kembali ke halaman query pesan')
                if(tema === ''){
                    alert('Tema tidak boleh kosong')
                }
            }else{
                let data = {kategori,tema}
                this.temukanPesan(data)
            }
        },
        responsiveControl(){
            var x = window.screen.width
            if(x < 500){
                //
            } else {
                //
            }
        }
    },
    mounted(){
        this.responsiveControl()
    }
}
</script>

